<template>
  <div
    :style="inlineStyle"
    class="o-card">
    <slot/>
  </div>
</template>

<script>
// 
export default {
  name: "Card",
  props: {
    gutter: {
      type: String,
      default: "14px"
    },
    innerGutter: {
      type: String,
      default: '24px'
    }
  },
  computed: {
    inlineStyle() {
      return `margin: ${this.gutter}; padding: ${this.innerGutter}`;
    },
  }
};
</script>

<style scoped>
.o-card {
  background-color: #fff;
}
</style>
